* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

body {
  font-family: "Open Sans", sans-serif;
}

.page {
  min-height: 100vh;
}
.page .content-aria {
  width: calc(100% - 250px);
  min-height: 100vh;
  transition: width 0.3s;
  float: right;
  background-color: #f2f1f6;
}
.page .content-aria.no-sidebar {
  width: 100%;
}
.page .page-head {
  margin-top: 1.25rem;
  padding: 1.25rem;
}
.page .page-head .page-title {
  font-weight: bold;
  color: #27ae60;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #292c33;
  color: #fff;
  padding: 1rem;
  transition: left 0.3s;
}
.page .sidebar.no-sidebar {
  left: -250px;
}
.page .sidebar .logo-area {
  font-weight: bold;
  font-size: 1.625rem;
  margin-bottom: 1rem;
}
.page .sidebar .links-area li a {
  display: block;
  position: relative;
  color: #ccc;
  padding: 10px 20px;
  transition: padding 0.3s, color 0.4s;
}
.page .sidebar .links-area li a:hover {
  color: #fff;
}
.page .sidebar .links-area > li > a {
  position: relative;
  color: #fff;
  font-size: 1.15rem;
}
.page .sidebar .links-area > li > a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  border: 2px solid #fff;
  transform: translateY(-50%);
  transition: background-color 0.3s;
}
.page .sidebar .links-area > li > a:hover {
  padding-left: 25px;
}
.page .sidebar .links-area > li > a:hover::before {
  background-color: #fff;
}
.page .sidebar .links-area .drop-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page .sidebar .links-area .drop-menu .toggle-submenu {
  transform: rotate(0deg);
  transition: transform 0.3s;
}
.page .sidebar .links-area .drop-menu .toggle-submenu.down {
  transform: rotate(90deg);
}
.page .sidebar .links-area .child-links {
  position: relative;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: max-height 0.5s ease, opacity 0.5s ease;
}
.page .sidebar .links-area .child-links a {
  position: relative;
}
.page .sidebar .links-area .child-links a::before {
  content: "";
  position: absolute;
  left: 3px;
  top: 0;
  width: 3px;
  height: 100%;
  background-color: #666;
  transition: background-color 0.3s;
}
.page .sidebar .links-area .child-links a:hover::before {
  background-color: #fff;
}
.page .sidebar .links-area .child-links.open {
  max-height: 218px;
  opacity: 1;
}

.header {
  display: flex;
  align-items: center;
  column-gap: 2rem;
  background-color: #fff;
  padding: 1.25rem;
}
.header .header-icon {
  background-color: #f9f9f9;
  padding: 0.625rem;
  border-radius: 50%;
  color: #555;
  cursor: pointer;
}
.header .toggle-sidebar {
  cursor: pointer;
  transition: color 0.3s;
}
.header .notifications-container .dropdown-menu {
  min-width: 400px;
  border: none;
  box-shadow: 0 0 10px #ddd;
}
.header .notifications-container .dropdown-menu .noti-head {
  background-color: #333;
  color: #fff;
  font-weight: bold;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .notifications-container .dropdown-menu .noti-content {
  padding: 1rem;
}
.header .notifications-container .dropdown-menu .noti-content ul li {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  font-size: 0.925rem;
  color: #777;
}
.header .notifications-container .dropdown-menu .noti-content ul li i {
  color: #fff;
  padding: 0.625rem;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .notifications-container .dropdown-menu .noti-content ul li i.fa-envelope {
  background-color: #4caf50;
}
.header .notifications-container .dropdown-menu .noti-content ul li i.fa-money-bill {
  background-color: #2196f3;
}
.header .notifications-container .dropdown-menu .noti-content ul li i.fa-thumbs-up {
  background-color: #ff9800;
}
.header .notifications-container .dropdown-menu .noti-content ul li:not(:last-child) {
  padding-bottom: 0.5rem;
}
.header .notifications-container .dropdown-menu .noti-footer a {
  display: block;
  padding: 0.625rem;
  border-top: 1px solid #eee;
}
.header .usermenu-container {
  margin-left: auto;
}
.header .usermenu-container .username {
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
  cursor: pointer;
}
.header .usermenu-container .username .avatar {
  width: 40px;
  height: 40px;
}
.header .usermenu-container .username .user {
  font-weight: bold;
  color: #777;
  margin-right: 0.25rem;
}
.header .usermenu-container .dropdown-menu {
  top: 5px !important;
  box-shadow: 0 0 10px #ddd;
  border: 1px solid #ddd;
  min-width: 500px;
  padding: 1rem;
}
.header .usermenu-container .dropdown-menu .user-info {
  padding: 0.25rem 0.5rem;
}
.header .usermenu-container .dropdown-menu .user-info h3 {
  font-weight: bold;
  font-size: 1.25rem;
}
.header .usermenu-container .dropdown-menu .user-info p {
  color: #888;
  font-size: 0.85rem;
}
.header .usermenu-container .dropdown-menu .activities-list {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.header .usermenu-container .dropdown-menu .activities-list li {
  padding: 0.625rem;
  font-size: 0.85rem;
}
.header .usermenu-container .usermenu-links .link-box {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 0.75rem;
  background-color: #eee;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
}
.header .usermenu-container .usermenu-links .link-box i.fa-gear {
  color: #2980b9;
}
.header .usermenu-container .usermenu-links .link-box i.fa-user {
  color: #16a085;
}
.header .usermenu-container .usermenu-links .link-box i.fa-power-off {
  color: #c0392b;
}

.settings-box {
  position: fixed;
  right: -250px;
  top: 0;
  width: 250px;
  height: 100%;
  background-color: #fff;
  z-index: 100;
  padding: 1.25rem;
  transition: right 0.4s;
}
.settings-box .option-title {
  font-weight: bold;
  color: #666;
  margin: 0.625rem 0 1rem;
}
.settings-box .toggle-settings {
  position: absolute;
  background-color: #fff;
  top: 100px;
  left: -40px;
  padding: 0.25rem 0.625rem;
  border-radius: 0.25rem 0 0 0.25rem;
  cursor: pointer;
}
.settings-box.show-settings-box {
  right: 0px;
}
.settings-box .color-options ul li:nth-child(1) {
  background-color: #27ae60;
}
.settings-box .color-options ul li:nth-child(2) {
  background-color: #2980b9;
}
.settings-box .color-options ul li:nth-child(3) {
  background-color: #c0392b;
}
.settings-box .color-options ul li:nth-child(4) {
  background-color: #d35400;
}
.settings-box .color-options ul li:nth-child(5) {
  background-color: #8e44ad;
}
.settings-box .color-options ul li {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  opacity: 0.7;
  cursor: pointer;
  display: inline-block;
}
.settings-box .color-options ul li.active {
  opacity: 1;
}